@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@500;600;700;900&display=swap);
*{margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;}

body {
  margin: 0;
  font-family: 'Epilogue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FAFAFA;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading {
  align-items: center;
  display: flex;
  justify-content: center;
}

label {
  font-size: 0.7em;
}

input{
  text-align: start;
  cursor: text;
  background-color: -internal-light-dark(rgb(255, 255, 255), rgb(59, 59, 59));
  padding: 1px 2px;
  border-width: 2px;
  border-radius: 5px;
}

button {
  position: relative;
  background-color: #1ed760;
  background-color: var(--background-base,#1ed760);
  color: #000000;
  color: var(--text-base,#000000);
  border-radius: 500px;
  font-size: inherit;
  padding: 4px 12px;
}

input:focus-within { 
  border: solid red
}

a{
  color: white;
}
.load{
  animation: is-rotating 1s infinite;
  width: 50px;
  height: 50px;
  border: 6px solid #e5e5e5;
  border-top: solid 6px rgb(30, 215, 96);
  border-radius: 50%;
  margin-top: 10px;
  
}

@keyframes is-rotating{
  to {
    transform: rotate(1turn);
  }}
.login-container{
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 40vh;
  justify-content: center;
  margin: auto;
  margin-top: 25vh;
  transform: translateY(-15vh);
  width: 50vw;
}

.logo-image{
  margin-top: -55px;
  margin-bottom: 15px;
}

.input-user {
  margin: 5px;
}

.login-btn {
  position: relative;
  background-color: #1ed760;
  background-color: var(--background-base,#1ed760);
  color: #000000;
  color: var(--text-base,#000000);
  border-radius: 500px;
  font-size: inherit;
  padding: 4px 12px;
}

.login-btn:hover {
  cursor: pointer;
  padding: 6px 14px;
}
.header-logo{
  width: 117px;
  margin-left: 35px;
  margin-top: -25px;
}



.first-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(18, 18, 18);
  color: white;
  height: 76px;
}

.img-perfil{
  max-width: 50px;
  border-radius: 50%;
  max-height: 30px;
  object-fit: cover;
}

.container-user{
  height: 30px;
  background-color: rgb(30, 215, 96);
  color: w;
  font-weight: 700;
  margin-right: 45px;
  border-radius: 15px;
  display: flex;
  justify-content: space-around;
  width: 180px;
  align-items: center;
}

ul {
  background-color: black;
  display: flex;
  justify-content: space-around;
}

.header-link li{
  background-color: rgb(51, 51, 51);
  color: white;
  text-align: center;
  flex-grow: 1;
  height: 58px;
  line-height: 58px;
  font-size: 24px;
}
.header-link li a {
  display: block;
  flex-basis: 200px;
  color: white;
}

.header-link li a:hover {
  background-color: rgb(30, 215, 96); 
  cursor: pointer;
}

@media (max-width: 600px) {
  .header-link li {
    font-size: 18px;
    height: 38px;
    line-height: 38px;
  }
  .container-user{
    width: 130px;
    margin-right: 20px;
  }
}
.search-container{
  margin-top: 35px;
  width: 50%;
  margin: 55px auto 20px auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search-container input {
  flex-basis: 300px;
  margin-right: 8px;
}

.search-container button {
  flex-basis: 100px;
}

.text-result {
  text-align: center;
  color: rgba(0, 0, 0, 0.74);
}

.search-Name{
  color: black
}

.album-container {
  background-color: white;
  flex-wrap: wrap;
  max-width: 80vw;
  margin: 30px auto;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
}

.album-container li {
  width: 180px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 0.8em;
  text-align: center;
  background-color: rgba(10, 10, 10, 0.787);
  padding-top: 10px;
  border-radius: 8px;
}

.album-name{
  width: 90%;
  text-align: center;
  padding: 5px;
}

.album-container li:hover{
  background-color: rgba(18, 18, 18, 1);
  padding: 1px;
}


.card-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
  background-color: rgb(37, 37, 37);
  color: white;
  height: 180px;
  width: 300px;
  border-radius: 20px;
}

.card-container label {
  display: flex;
  align-items: center;
  height: 30px;
  font-size: 1em;
  grid-gap: 5px;
  gap: 5px;
}

audio {
  max-width: 250px;
  height: 40px;
}

.album-musics span {
  text-align: center;
  font-size: 1em;
  color: #1ed760;
}

.card-container:hover {
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.644);
  opacity: 0.9;
}

@media (max-width: 400px) {
  .card-container {
    width: 200px;
  }
  audio {
    width: 150px;
    height: 30px;
  }
}
.album-musics {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.album-image {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 60px;
}


.album-image img {
  flex-basis: 150px;
  object-fit: cover;
  margin-bottom: 10px;  
}

.tracks-container {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
}
.card-musics{
  display: flex;
  flex-wrap: wrap;
  grid-gap: 15px;
  gap: 15px;
  margin: 30px auto;
  justify-content: center;
}

.favorites-title {
  text-align: center;
  margin-top: 20px;
}

.card-musics span {
  color: #1ed760;
  text-align: center;
}

.edit-infos{
  margin: 100px auto;
  /* border: solid 1px; */
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 300px;
  grid-gap: 18px;
  gap: 18px
}

.edit-infos label {
  display: flex;
  align-items: center;
  font-size: 1.3em;
}

.edit-infos label input {
  width: 200px;
  text-align: center;
}

.edit-infos button {
  align-self: center;
  margin-left: 35px;
}

@media (max-width: 400px) {
  .edit-infos label{font-size: 1em;}
  .edit-infos label input {
    width:100px;
  }
  .edit-infos{width: 150px;}
}
.profile-edit{
  color: white;
  position: relative;
  background-color: #1ed760;
  background-color: var(--background-base,#1ed760);
  color: #000000;
  color: var(--text-base,#000000);
  border-radius: 500px;
  font-size: inherit;
  padding: 4px 12px;
  align-self: center;
  margin-top: 20px;
}

.profile-edit:hover{
  cursor: pointer;
  /* padding: 6px 12px; */
  box-shadow: black 3px 3px 13px;
}

.div-container{
  display: flex;
  margin: 45px auto;
  border: 1px solid;
  max-width: 600px;
  flex-direction: column;
  align-items: center;
  min-height: 150px;
  background-color: whitesmoke;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.541);
  word-wrap: break-word;
  padding: 15px 0;
  border-radius: 8px;
}

.div-container img {
  width: 220px;
  align-self: center;
  object-fit: cover;
  border-radius: 50%;
  max-height: 180px;
}

.div-container p {
  color: rgb(3, 2, 2);
  font-weight: 600;
  max-width: 300px;
  margin-top: 15px;
}

.div-container p span {
  font-weight: 500;
  color: black;
  max-width: 300px;
}

.text-container {
  align-self: center;
}

@media (max-width: 650px) {
  .div-container {
    max-width: 350px;
  }
}

@media (max-width: 415px) {
  .div-container {
    max-width: 250px;
  }
  .div-container img {
    width: 215px;
  }
}
